
.register {
  height: 85%;
}
.register__caption {
  display: block;
  font-size: 4em;
  color: #333333;
  margin: 4%;
  margin-left: 5%;
  border-bottom: 4px solid #333333
}
.register__cloth-form {
  width: 90%;
  height: 80%;
  margin: 0 auto;
}
.register__cloth-form input {
  display: block;
  margin: 0 auto;
  width: 90%;
  height: 1em;
  font-size: 3em;
}
.register__clothes-label {
  display: block;
  font-size: 3em;
  color: #333333;
  margin: 2% auto;
  border-bottom: 4px solid #333333;
  width: 95%;
}
.register__clothes-label-optional {
  font-size: 0.5em;
  color: #666666;
}

/* color picker css */
.register__clothes-color {
  height: 200px !important;
  margin: 0 auto;
  width: 90% !important;
}
.vue-color__compact__color-item {
  height: 60px !important;
  width: 60px !important;
}
.register__clothes-submit {
  background-color: darkorange;
  color: #ffffff;
  border: 0;
  font-size: 4em;
  padding: 1% 5%;
  position: absolute;
  right: 5%;
}



