
.signup__inputData {
  width: 80%;
  height: 90%;
  margin: 5% auto;
  font-size: 3em;
}
.signup__inputData input {
  font-size: 1em;
  border: 0;
  width: 100%;
  margin: 5% 0;
  border-bottom: 3px solid #01288d;
}
.signup__selectLocation {
  -webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;

  margin-top: 5%;
  padding: 3%;
  width: 100%;
  height: 8%;
  border: 0;
  background-color: #01288d;
  color: white;
  font-size: 1.5em;
}
.signup__selectLocation option {
    font-size: 30%;
}
.signup__submit {
  font-size: 1.3em;
  border: 0;
  margin: 5% 0;
  padding: 1% 3%;
  position: absolute;
  right: 10%;
  background-color: darkorange;
  color: #ffffff;
}
.signup__selectSex {
  border-bottom: 4px solid #01288d;
}
.signup__selectSex input {
  width: 1em;
  height: 1em;
  margin: 2% 5%;
}
.signup__inputData-error {
  background-color: indianred;
  width: 90%;
  text-align: center;
  padding: 5% 2%;
  margin: 2% auto; 
  border-radius: 10px;
  color: #ffffff;
}



