
.top__weather-icon-wrapper {
  height: 90%;
  width: 100%;
  position: absolute;
  top: 0;
  background-color: white;

  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: rotateY(180deg);
}
.top__weather-icon {
  width: 50%;
  height: 50%;
  margin: 25% auto 0 auto;
  display: block;
}
.top__weather-data {
  width: 100%;
  height: 25%;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
}



