
.top__clothes-image {
    width: 100%;
    height: 80%;
    display: -ms-flexbox;
    display: flex;
    position: absolute;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}
.top__clothes-image-info {
    z-index: 100;
    position: absolute;
    right: 0;
    top: 0%;
    color: white;
    font-weight: 900;
    font-size: 3em;
}
.top__clothes-image-info img {
    width: 15%;
    display: block;
    position: relative;
    left: 85%;
}
.top__clothes-image-full {
    width: 100%;
    height: 80%;
    margin: 0 auto;
    transform: rotateY(180deg);
}
.top__clothes-image-full > div {
    width: 70%;
    margin: 0 auto;
}



