
.header__menu-content {
  position: relative;
  top: 3em;
  font-size: 2em;
  margin: 0 auto;
  padding: 0;
  width: 90%;
  height: 90%;
}
.open {
  width: 70vw;
  height: 130vh;
  position: fixed;
  top: 0;
  right: -70vw;
  z-index: 990;
  background-color: #888888;
  transition: transform .6s;
  transform: translate(-70vw);
}
.close {
  width: 70vw;
  height: 130vh;
  position: fixed;
  top: 0;
  right: -70vw;
  z-index: 990;
  background-color: #888888;
  transition: transform .6s;
  transform: translate(70vw);
}
.header__menu-content-buttons {
  width: 100%;
  height: 6%;
  display: -ms-flexbox;
  display: flex;
}
.header__menu-content-signup {
  margin: 0 3%;
  padding: 10px;
  padding-top: 3%;
  width: 50%;
  border-radius: 10px;
  text-decoration: none;
  text-align: center;
  color: #ffffff;
  background-color: #ffa500;
}
.header__menu-content-signin {
  margin: 0 3%;  
  padding: 10px;
  padding-top: 3%;
  width: 50%;
  border-radius: 10px;  
  text-decoration: none;
  text-align: center;
  color: #444444;
  background-color: #ffffff;
}
.header__menu-content-linkList {
  margin: 0;
  padding: 0;
  padding-top: 10%;
}
.header__menu-content-linkList li {
  list-style: none;
  padding: 5% 0;
  border-bottom: 3px solid #222222;
}
.header__menu-content-logout {
  position: absolute;
  bottom: 35%;
  right: 3%;
  width: 50%;
}
.header__menu-content-logout-button {
  border: 0;
  border-radius: 10px;
  color: #ffffff;
  background-color: #444444;
  font-size: 1em;
  padding: 3% 5%;
}



